.channel {
    width: 152px;
    height: 158px;
    background-color: #F7F7F7;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;
    margin-bottom: 38px;
}

.channelTitle {
    font-family: Open Sans;
    font-size: 16px;
    font-weight: 700;
    line-height: 24px;
    margin-top: 27px;
    margin-bottom: 16px;
}